<template>
	<div>
		<Breadcrumb class="bread">
			<div class="title">虚拟会议室</div>
			<div class="b_func">
				<div class="searchForm">
					<RadioGroup v-model="searchType" class='radio'>
						<Radio label="表决中"></Radio>
						<Radio label="通过"></Radio>
						<Radio label="未通过"></Radio>
					</RadioGroup>
					<div>
						<Input  placeholder="会议查询" v-model="searchInput"  style="width:300px">
							<Button slot="append" @click="searchVote(searchInput)">
								<Icon type="ios-search" size="16"/>
							</Button>
						</Input>
					</div>
				</div>
				<div class="addVote">
					<Button type="primary" size="large" @click="addVote()" icon="md-barcode" >发起决议</Button>
				</div>
			</div>
		</Breadcrumb>
		<Row v-if="mouldBoard">
			<Col  :xs="24" :sm="12" :lg="8">
				<Card :bordered="false" class="voteItem">
					<p slot="title">增发10个Token</p>
					<div class="msgBoard">
						<p><b>时间</b>2018-11-16</p>
						<p><b>状态</b>审核中</p>
						<p><b>发起人</b>王一明</p>
						<p><b>同意<span>(50%可通过审核)</span></b> <Progress :percent="45" status="active" /></p>
						<p><b>否决</b> <Progress :percent="25" class="warningProgress"/></p>
						<div class="btn">
							<Button type="success" size="large" icon="md-albums">同意</Button>
							<Button type="info" size="large" icon="md-albums">否决</Button>
						</div>
					</div>
				</Card>
			</Col>
			<Col  :xs="24" :sm="12" :lg="8">
				<Card :bordered="false" class="voteItem">
					<p slot="title">增发10个Token</p>
					<div class="msgBoard">
						<p><b>时间</b>2018-11-16</p>
						<p><b>状态</b>审核中</p>
						<p><b>发起人</b>王一明</p>
						<p><b>同意<span>(50%可通过审核)</span></b> <Progress :percent="45" status="active" /></p>
						<p><b>否决</b> <Progress :percent="25" class="warningProgress"/></p>
						<div class="btn">
							<Button type="success" size="large" icon="md-albums">同意</Button>
							<Button type="info" size="large" icon="md-albums">否决</Button>
						</div>
					</div>
				</Card>
			</Col>
			<Col  :xs="24" :sm="12" :lg="8">
				<Card :bordered="false" class="voteItem">
					<p slot="title">增发10个Token</p>
					<div class="msgBoard">
						<p><b>时间</b>2018-11-16</p>
						<p><b>状态</b>审核中</p>
						<p><b>发起人</b>王一明</p>
						<p><b>同意<span>(50%可通过审核)</span></b> <Progress :percent="45" status="active" /></p>
						<p><b>否决</b> <Progress :percent="25" class="warningProgress"/></p>
						<div class="btn">
							<Button type="success" size="large" icon="md-albums">同意</Button>
							<Button type="info" size="large" icon="md-albums">否决</Button>
						</div>
					</div>
				</Card>
			</Col>
		</Row>
		
		<div class="mouldBoard" v-else>
			<Card :bordered="false" class="mouldItem">
				<div class="msgBoard">
					<p>暂无表决记录，立即发起表决</p>
					<Button type="primary" @click="addVote()" size="large" icon="md-barcode" >发起决议</Button>
				</div>
			</Card>
		</div>
		<Drawer title="新的表决" width="350" :closable="false" v-model="isAddDrawer" class="newVoteBoard">
			<div>
				<h4>新的表决</h4>
				<p>问题</p>
				<Input v-model="addVoteInput" placeholder="Enter something..." />
				<div class="btn">
					<Button type="success" long>开始投票</Button>
					<p class="hint">发起投票的者默认选择同意</p>
				</div>
			</div>
		</Drawer>
		
	</div>
</template>

<script>
	export default {
		data(){
			return{
				searchInput: '',
				searchType: '',
				addVoteInput: '',
				mouldBoard: true,
				isShowDrawer: false,
				isAddDrawer: false,
				
				pStyle: {
                    fontSize: '16px',
                    color: 'rgba(0,0,0,0.85)',
                    lineHeight: '24px',
                    display: 'block',
                    marginBottom: '16px'
                }
			}
		},
		methods:{
			searchVote(vals){
				console.log('发送请求！');
			},
			addVote(){
				console.log('添加一个');
				this.isAddDrawer = !this.isAddDrawer
			},
			detailDrawer(){
				this.isShowDrawer = !this.isShowDrawer
			}
		}
	}
</script>
<style>
	.warningProgress .ivu-progress-bg{
		background-color: #ed4014
	}
		
			
</style>

<style scoped lang="stylus">
	flexs()
		display:flex
		align-items: center
		justify-content: space-between
		
	.bread
		flexs()
		.b_func
			flexs()
			flex-basis: 800px
			.searchForm
				flexs()
				.radio
					margin-right: 30px
			.addVote
				margin-right: 30px
			
	.voteItem
		min-height: 350px
		margin: 0 30px 30px 0
		.msgBoard
			padding: 20px 16px;
			p
				margin-top: 14px
				b
					margin-right: 16px
					display: inline-block
					min-width: 100px
				&:first-child
					margin-top: 0
			
			.btn
				text-align: right
				margin-top: 30px
	
	.mouldBoard
		margin-top: 100px
		flexs()
		justify-content: center
		.mouldItem
			width: 250px
			height: 300px
			flexs()
			.msgBoard
				width: 250px
				padding: 0 20px
				text-align:center
				p
					color: #9ea7b4
					margin-bottom: 20px
	
	.newVoteBoard
		h4
			margin-bottom:20px
		p
			margin-bottom:5px
			&.hint
				color: #9ea7b4
				margin-top: 5px
		.btn
			margin-top: 50px
			
</style>


